import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./report-jobcard-productivity.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ], 
})

export class ReportJobCardComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	listHide: boolean = true;
	animateList: string = 'in';
	reportJobCardProductivity=new ReportJobCardProductivityData();
	compdetails=new CompDetails();
	jcTypes=[];
	jcCategories=[];
	jcStatus=[];
	bootsjcStatus=[];
	msg=[];
	bootstrapFormValues=[];
	listJobCardExecutiveData=[];
	technicianDetailsData=[];
	branches=[];
	franchisees=[];
	jcstartdatemsg:string;
	jcenddatemsg:string;
	bootstrapMsg:string='';
	compBranchCount:string;
	compFranchiseeCount:string;
	ReportJobCardProductivityDataSummary=[];
	datafields=[
		{key:'1', name:'RO' },
		{key:'2',name:'Labour'},
		{key:'3',name:'Parts'},
		{key:'4',name:'VAS'},
		{key:'5',name:'Oil'},
		{key:'6',name:'Brake'},
		{key:'7',name:'Tyre'},
		{key:'8',name:'Battery'},
		{key:'9',name:'Accessories'},
		{key:'10',name:'Ext. Warranty'},
	];
	ReportJobCardProductivityForm: FormGroup; 

 	constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.compdetails.compBranchCount = sessionStorage.getItem('compBranchCount');
	this.compdetails.compFranchiseeCount = sessionStorage.getItem('compFranchiseeCount');
		console.log(this.compdetails.compBranchCount);
		console.log(this.compdetails.compFranchiseeCount);
	}
	
    ngOnInit() { 
		
		this.listHide = true;
		this.getTodaysDate();
		this.populateDropDownData();
		this.ReportJobCardProductivityForm = new FormGroup({	
			'jctypeIds' : new FormControl(''), 
			'jccatIds' : new FormControl(''), 
			'jcstatusIds' : new FormControl(''), 
		});
		
    }
	  
	get jctypeIds(){ return this.ReportJobCardProductivityForm.get('jctypeIds')};
	get jccatIds(){ return this.ReportJobCardProductivityForm.get('jccatIds')};
	get jcstatusIds(){ return this.ReportJobCardProductivityForm.get('jcstatusIds')};
	  
   populateData(){

	}
	 
	 populateDropDownData(){
		  	
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/jobcard-type',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.jcTypes = data['populateJcType']
																	}); 
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/jobcard-status',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.jcStatus = data['populateJcStatus']
																	}); 
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/jobcard-category',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.jcCategories = data['populateJcCategory']
																	}); 
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.branches = data['populateBranch']
																	});
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/franchisee',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.franchisees = data['populateFranchisee']
																	}); 
		  
	
	 }
	   
	 ngAfterViewInit() {
		 $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){});
     }
	 
 	 toggleAnimation(){
			
		 }
		  
	 getTodaysDate(){
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/currentdate',
							 			JSON.stringify({ requestType : 'shortDate' }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.reportJobCardProductivity.startDate= data['date'];
				  														this.reportJobCardProductivity.endDate= data['date'];
			  															});
	 }

	onSubmit() {
		 this.listHide = false;
		 this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				console.log(this.bootstrapFormValues[i].name);
				this.reportJobCardProductivity[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		
		for(var i=0;i<this.bootstrapFormValues.length;i++){
				if(this.bootstrapFormValues[i].name=="startDate"){
					console.log(this.bootstrapFormValues[i].value);
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Start Date!<br>';
						this.jcstartdatemsg="<font class='errorMsg'>Enter Start Date</font>";
						
					}else{
						this.jcstartdatemsg = "";
					}
				}
			if(this.bootstrapFormValues[i].name=="endDate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter End Date!<br>';
						this.jcenddatemsg="<font class='errorMsg'>Enter End Date</font>";
						
					}else{
						this.jcenddatemsg = "";
					}
				}	
		}
		if(this.bootstrapMsg != ''){
				this.msg[0]='Error';
			 	this.msg[1]='Enter the Mandatory Fields' ;
				this.toast.showtoast(this.msg);
				this.bootstrapMsg='';
			}
		if(!this.ReportJobCardProductivityForm.invalid && this.bootstrapMsg == ''){
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/service/report-jobcardproductivity',
								 JSON.stringify(this.reportJobCardProductivity),
								{ headers, withCredentials: true }).subscribe(data =>{
//			   						if(data['errormessage']==''){
			   					this.listJobCardExecutiveData = data['listJobCardExecutive'];
			   					this.technicianDetailsData = data['technicianDetails'];
//									}
			   console.log(this.listJobCardExecutiveData.length);
			   console.log(this.technicianDetailsData.length);
			   						if(data['errormsg'] != undefined){
									this.msg[0]='Error';
									this.msg[1]=data['errormessage'];
									}
		   							},
			   						err =>{
										this.msg[0]='Error';
										this.msg[1]=err['error'];
										this.toast.showtoast(this.msg);
	  }); 
	}
	  }
	   
	

  

	}
export class ReportJobCardProductivityData {
	
startDate: string='';
endDate: string='';
jctypeIds: string[];
jccatIds: string[];
jcstatusIds: string[];
jcBranchIds: string[];
jcFrancheseeIds: string[];
	 
}

export class CompDetails{
	
	compFranchiseeCount:string='0';
	compBranchCount:string='0';
}


